/*
---
name: gnav(グローバルナビゲーション)
category:
  - common/header/gnav
tag:
  - base
compile: false
---

見出し関連のサイズを調整します。
サンプルとして、共通パーツのstyleguideを掲載

```html
<nav class="gnav">
  <ul class="gnav__row">
    <li class="gnav__item">
      <a href="#">home</a>
    </li>
    <li class="gnav__item">
      <a href="#">sample</a>
    </li>
    <li class="gnav__item">
      <a href="#">menu</a>
    </li>
    <li class="gnav__item">
      <a href="#">menu</a>
    </li>
    <li class="gnav__item">
      <a href="#">menu</a>
    </li>
    <li class="gnav__item_contact">
      <a href="#">contact</a>
    </li>
  </ul>
</nav>
```
*/
.gnav{
	position: relative;
	z-index: 3;
	float: right;
	@include max-screen($width-s){
		z-index: 1;
		position: absolute;
		top:$header-height;
		left: 0;
		float: none;
		width: 100%;
		transition: transform .5s;
		transform: translate3d(0,-400px,0);
		&.active{
			transition: transform .5s;
			transform: translate3d(0,0,0);
		}
	}
	&__row{
		display: flex;
		@include max-screen($width-s){
			display: block;
		}
	}
	&__item{
		$this:&;
		a{
			position: relative;
			display: block;
			box-sizing: border-box;
			height:  $header-height;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0 $bm;
			text-decoration: none;
			background-color: #fff;
			@include touch-hover(){
				background-color: $color-light;
				color:#fff;
			}
			@include max-screen($width-s){
				justify-content: flex-start;
				border-bottom: 1px solid $color-light;
				&::after{
					@include icon-font();
					position: absolute;
					top:0;
					right: $bm-inner;
					bottom: 0;
					display: flex;
					align-items: center;
					justify-content: center;
				}
			}
		}
		&_home{
			@extend #{$this};
			body.home & a{
				background-color: $color-light;
				color:#fff;
			}
		}
		&_sample{
			@extend #{$this};
			body._sample & a{
				background-color: $color-light;
				color:#fff;
			}
		}
		&_sample{
			@extend #{$this};
			body.sample & a{
				background-color: $color-light;
				color:#fff;
			}
		}
    &_layout{
			@extend #{$this};
			body._layout & a{
				background-color: $color-light;
				color:#fff;
			}
		}
		&_contact{
			@extend #{$this};
			a{
				background-color: $color-point;
				color:$color-light;
				body.contact &{
					background-color: $color-light;
					color:#fff;
				}
			}
		}
	}//item

}
